
import { useState,useEffect } from 'react';
import { useEthers,useCall,useCalls,useTokenBalance,useEtherBalance } from "@usedapp/core";
import { formatEther } from '@ethersproject/units'
import { Contract } from "@ethersproject/contracts";
import { H3,Text,StackItem,HStack,DataTableRow } from '@lidofinance/lido-ui';
import {StatHolder,StatLabel,StatValueHolder} from "./stakingStyles.js";

const BalanceViewer = function(props){

    const config = props.settings;

    const { account } = useEthers();

    const shareBalance = useTokenBalance(config.contract.address, account);
    const [pricePerShare,setPricePerShare] = useState(0);
    const [bApy,setbApy] = useState(0);
    const [sApy,setsApy] = useState(0);

    function estApy(exchange){

        var start = 1657425600000;
        var timePassed = (new Date()-start)/(1000*60*60*24*365);
        var returns = Math.pow(Math.E,Math.log(exchange)*(1/timePassed));
        return returns;
    }
    const calls = [config.contract.address,config.vaultAddress].map(address => ({
        contract: new Contract(address, config.contract.abi),
        method: 'getPricePerFullShare',
        args: []
      })) ?? []
    const results = useCalls(calls) ?? [];


    useEffect(function(){
        if(results[0]&&results[1]){

            setsApy(estApy(formatEther(results[0].value[0])-0));

            if(config.LP){
                (async () => {
                  try {
                    const response = await fetch("https://api.beefy.finance/lps");
                    const json = await response.json();
                    setPricePerShare((formatEther(results[0].value[0])-0)*(formatEther(results[1].value[0])-0)*json[config.beefyVault]);
        
                    
                  } catch (error) {
                    console.log("error", error);
                  }
                })();
            }else{
                setPricePerShare((formatEther(results[0].value[0])-0)*(formatEther(results[1].value[0])-0));
            }

            
            //pricePerShare.set(results[0].value.toNumber());
        }
    },[results]);

    useEffect(() => {
        const url = "https://api.beefy.finance/apy";
    
        const fetchData = async () => {
          try {
            const response = await fetch(url);
            const json = await response.json();

            setbApy(json[config.beefyVault]);
          } catch (error) {
            console.log("error", error);
          }
        };
    
        fetchData();
    }, []);

    const stats = [{
        'label':'Shares',
        'value':shareBalance?(formatEther(shareBalance)*config.sharemult).toFixed(3):'--'
    },{
        'label':'Value',
        'value':(shareBalance&&pricePerShare)?(formatEther(shareBalance)*pricePerShare).toFixed(3)+' '+config.symbol:'--'
    },{
        'label':'est APY',
        'value':(bApy&&sApy)?(((bApy+1)*sApy-1)*100).toFixed(2)+'%':'',
        'color':'#00bcd4',
        'help': (<p>Vault APY: {(bApy*100).toFixed(2)}% <br/> Reflection APY: {(sApy*100-100).toFixed(2)}% <br/> Estimated APY based on past data. This is not a guarantee of return or profit, and may fluctuate based on market conditions</p>)
    }]

    var maxlen = 0;
    for(var i=0;i<stats.length;i++){
        if(stats[i].value.length>maxlen){
            maxlen = stats[i].value.length;
        }
    }
    var fontsz = maxlen>=10?18-(maxlen-10)/2:18+'px';
    // results.forEach((result, idx) => {
    //     if(result && result.error) {
    //         console.error(`Error encountered calling 'totalSupply' on ${calls[idx]?.contract.address}: ${result.error.message}`)
    //     }
    //     console.log(result)
    // })

    return (
        <div style={{padding:'32px',textAlign:'left',fontWeight:'500'}}>
            <HStack>
                {stats.map(function(stat){
                    return (
                        <StackItem key={stat.label} basis="33%">
                            <StatHolder>
                                <StatLabel><DataTableRow title={stat.label} help={stat.help} /></StatLabel>
                                <StatValueHolder>
                                    <span style={{fontSize:fontsz,color:stat.color||''}}>{stat.value}</span>
                                </StatValueHolder>
                            </StatHolder>
                        </StackItem>
                    )
                })}
                
            </HStack>
        </div>
            
    );
}
export default BalanceViewer;